const currentDate = new Date();
const month = currentDate.getMonth() + 1;
const year = currentDate.getFullYear();
const day = currentDate.getDate();

const accordions = [
    {
        id: "BirthMonth",
        title: "Cumpleaños del Mes",
        content: {
            source: `/api/students?date_month_birth=${month}`,
            notFound: "Nadie cumple años este mes...",
            formatAs: formatBirthMonth
        }
    },
    {
        id: "AnnivMonth",
        title: "Aniversarios del Mes",
        content: {
            source: `/api/students?date_month_anivs=${month}&date_year_anivs=${year}`,
            notFound: "Nadie festeja su aniversario este mes...",
            formatAs: formatAnnivMonth
        }
    },
    {
        id: "PaymentMonth",
        title: "Pagos del Mes",
        content: {
            source: `/api/students?compare_gt_fee=pay-current`,
            notFound: "Nadie debe pagos de este mes...",
            formatAs: formatPay
        }
    },
];

const notifications = [
    {
        id: "BirthDay",
        content: {
            source: `/api/students?date_month_birth=${month}&date_dayOfMonth_birth=${day}`,
            notFound: "Nadie cumple años hoy...",
            formatAs: formatBirthDay
        }
    },
    {
        id: "AnnivDay",
        content: {
            source: `/api/students?date_month_anivs=${month}&date_year_anivs=${year}&date_dayOfMonth_anivs=${day}`,
            notFound: "Nadie festeja su aniversario hoy...",
            formatAs: formatAnnivDay
        }
    }
];

function formatBirthDay(student) {
    return `El cumpleaños de ${student.fullname} es hoy! Felicitalo!`;
}

function formatAnnivDay(student) {
    const enrollDate = new Date(student.enroll);
    const monthDifference = 12 * (currentDate.getFullYear() - enrollDate.getFullYear()) + currentDate.getMonth() - enrollDate.getMonth();
    return `El aniversario de ${monthDifference} meses de ${student.fullname} es hoy! Felicitalo!`;
}

function formatBirthMonth(student) {
    const birthDate = new Date(student.birth);
    return `El cumpleaños de ${student.fullname} es ${birthDate.getUTCDate()} /  ${birthDate.getUTCMonth() + 1} / ${birthDate.getUTCFullYear()}`;
}

function formatAnnivMonth(student) {
    const enrollDate = new Date(student.enroll);
    const monthDifference = 12 * (currentDate.getFullYear() - enrollDate.getFullYear()) + currentDate.getMonth() - enrollDate.getMonth();
    return `${student.fullname} celebra ${monthDifference} meses en Kumon`;
}

function formatPay(student) {
    return `${student.fullname} ha pagado $${student["pay-current"]} / $${student.fee}`;
}

export {accordions, notifications};