import useRegisterSubmit from "../../hooks/useRegisterSubmit.js";
import useUpdateSubmit from "../../hooks/useUpdateSubmit.js";
import StudentSubmit from "../../hoc/StudentSubmit.js";

const StudentRegister = StudentSubmit(useRegisterSubmit);
const StudentUpdate = StudentSubmit(useUpdateSubmit);

class StudentFields {
    constructor(fields) {
        this.fields = fields;
    }
    reset() {
        for (const field in this.fields) { delete this.fields[field].defaultValue; }
    }
    add(fields) {
        this.fields = {...this.fields, fields};
    }
}

export {StudentRegister, StudentUpdate, StudentFields};


