import React, {createContext, useState} from "react";

const ToggleContext = createContext({status: null, setStatus: () => {}});

function Toggleable({initialStatus = false, className, children}) {
    const [status, setStatus] = useState(initialStatus);
    return (
        <ToggleContext.Provider value = {{status, setStatus, timestamp: Date.now()}}>
            <div className = {className}>{children}</div>
        </ToggleContext.Provider>
    );
}

export {Toggleable, ToggleContext};