import React, {useState, useEffect} from "react";

function Card({children, type}) {
    return (
        <div className = {`card-${type}`}>{children}</div>
    );
}

function FlashCard({children, type, duration}) {
    const isExpired = duration > 0 ? false : true;
    const [expired, setExpired] = useState(isExpired);
    useEffect(() => { 
        if (!expired) {
            let disappear = setTimeout(() => setExpired(true), duration * 1000);
            return () => { clearTimeout(disappear) };
        }
    });
    return (!expired && <Card type = {type}>{children}</Card>);
}

export {Card, FlashCard};