import React, {useRef} from "react";

function StudentSubmit(useStudentSubmit) {
    return function StudentRegister({fields, renderSubmitted, renderError}) {
        const [studentRef, onStudentInput] = useStudentInput();
        const [formSubmitted, formError, formFields, onStudentSubmit] = useStudentSubmit(studentRef, fields);
        return (
            <div className = "app-register">
                <form onSubmit = {onStudentSubmit}>
                    {Object.keys(formFields).map(field => {
                        return <label key = {field}>
                            {formFields[field].label}
                            <input 
                            type = {formFields[field].type || "text"}
                            defaultValue = {formFields[field].defaultValue || ""} 
                            name = {field}
                            onInput = {onStudentInput}
                            required = {true}
                            />
                        </label>
                     })}
                    <button type = "submit" className = "register-btn">Subir</button>
                    {renderSubmitted && renderSubmitted(formSubmitted)}
                    {(renderError && formError) && renderError(formError)}
                </form>
            </div>
        );
    }
}

function useStudentInput() {
    const studentRef = useRef({});
    function onStudentInput(event) {
        Object.assign(studentRef.current, {[event.target.name]: event.target.value});
    }
    return [studentRef, onStudentInput];
}

export default StudentSubmit;