import React from "react";
import {Toggleable} from "../components/toggleable/Toggleable.js";
import {ToggledState} from "../components/toggleable/ToggledState.js";
import {Toggling, Toggler, TogglerButton} from "../components/toggleable/Toggled.js";
import StudentContainer from "../components/student/StudentInfo.js";
import {notifications} from "./home/ApiLists.js";

function Notifications() {
    return ( 
        <Toggleable initialStatus = {false} className = "notification-panel">
            <Toggler className = "notification-button">
                <TogglerButton caption = "Ver"/>
            </Toggler>
            <Toggling>
                <ToggledState status = {[true]}>
                    <NotificationsDisplay />
                </ToggledState>
                <ToggledState status = {[false]}>
                    {null}
                </ToggledState>
            </Toggling>
        </Toggleable>
    );
}

function NotificationsDisplay() {
    return (
        <div className = "notifications">
            {notifications.map(notification => (
                <StudentContainer key = {notification.id}
                source = {notification.content.source}
                notFound = {notification.content.notFound}
                >
                    {student => <p>{notification.content.formatAs(student)}</p>}
                </StudentContainer>
            ))}
        </div>
    );
}

export default Notifications;