import React, {useContext} from "react";
import axios from "axios";
import {Toggleable, ToggleContext} from "../../components/toggleable/Toggleable.js";
import {StudentContext} from "../../context/StudentContext.js";
import {Toggler, TogglerButton, Toggling} from "../../components/toggleable/Toggled.js";
import {ToggledState} from "../../components/toggleable/ToggledState.js";

function StudentUnsubButton({caption}) {
    const studentUUID = useContext(StudentContext).uuid;
    function onUnsubConfirm() {
	    axios.delete(`/api/students/delete?uuid=${studentUUID}`)
        .then(response => window.location.href = "/")
        .catch(err => undefined); //DO SOMETHING
    }
    return (
        <Toggleable initialStatus = {false}>
            <Toggler>
                <TogglerButton caption = {caption} status = {true} />
            </Toggler>
            <Toggling>
                <ToggledState status = {[true]}>
                    <TogglerButton caption = "Cancelar" status = {false} />
                    <button onClick = {onUnsubConfirm}>Confirmar</button>
                </ToggledState>
            </Toggling>
        </Toggleable>
    );
}

function StudentEditButton({caption}) {
    const toggleable = useContext(ToggleContext);
    const onEdit = () => toggleable.setStatus(1);
    return (
        <button onClick = {onEdit}>{caption}</button>
    );
}

export {StudentUnsubButton, StudentEditButton};