import {useState} from "react";
import {useRegularFields} from "./useFields";
import axios from "axios";

function useRegisterSubmit(studentRef, studentFields) {
    const [formSubmit, setFormSubmit] = useState({submitted: [], issued: 0});
    const [formError, setFormError] = useState({error: null, issued: 0});
    const [formFields] = useRegularFields(studentFields);
    function addSubmitted() {
        setFormSubmit(previous => (
            {submitted: [...previous.submitted, 
            {student: studentRef.current, issuedAt: Date.now(), issuedToken: previous.issued + 1}], 
            issued: previous.issued + 1}
        ));
    }
    function onRegisterSubmit(event) {
        event.preventDefault();
        const student = studentRef.current;
        let form = event.target;
        axios.post("/api/students", student)
        .then(response => { addSubmitted(); form.reset(); form = null; studentRef.current = {}; })
        .catch(err => setFormError(previous => (
            {error: {failure: err, issuedAt: Date.now(), issuedToken: previous.issued + 1},
            issued: previous.issued + 1}
        )));
    }
    return [formSubmit.submitted, formError.error, formFields, onRegisterSubmit];
}

export default useRegisterSubmit;