import React, {useContext} from "react";
import {ToggleContext} from "./Toggleable";

function Toggler({className, children}) {
    return (
        <div className = {className}>{children}</div>
    );
}

function TogglerButton(props) {
    const {caption, status, ...buttonProps} = {...props};
    const toggleable = useContext(ToggleContext);
    const toggledStatus = status || !toggleable.status;
    return (
        <button onClick = {() => toggleable.setStatus(toggledStatus)} {...buttonProps}>
            {caption}
        </button>
    )
}

function Toggling({children}) {
    return (<>{children}</>);
}

export {Toggler, TogglerButton, Toggling};