import React from "react";
import {Toggleable} from "../../components/toggleable/Toggleable.js";
import {Toggling} from "../../components/toggleable/Toggled.js";
import {ToggledState} from "../../components/toggleable/ToggledState.js";
import {StudentUpdate, StudentFields} from "../../components/apps/StudentSubmit.js";
import {FlashCard} from "../../components/cards/Cards.js";
import StudentInfo from "./StudentInfo.js";

function StudentApps() {
    return (
        <Toggleable initialStatus = {0}>
            <Toggling>
                <ToggledState status = {[0, 1]}>
                    <StudentInfo />
                </ToggledState>
                <ToggledState status = {[1]}>
                    <StudentUpdate fields = {fields}
                    renderError = {error => {
                        const expiresIn = 2.5 - (Date.now() - error.issuedAt) / 1000;
                        return expiresIn > 0
                        ? <FlashCard type = "error" duration = {expiresIn} key = {error.issuedToken}>
                            {"Hubo un problema para actualizar alumno!"}
                        </FlashCard>
                        : null;
                    }}
                    />
                </ToggledState>
            </Toggling>
        </Toggleable>
    );
}  

const fields = new StudentFields(
    { 
        name: {label: "Nombre:"},
        lastname: {label: "Apellido:"},
        enroll: {label: "Fecha de Ingreso", type: "date"},
        birth: {label: "Fecha de Nacimiento", type: "date"},
        level: {label: "Nivel"},
        fee: {label: "Cuota"},
        "pay-current": {label: "Pago de Mes Actual"},
        "pay-last": {label: "Pago de Mes Anterior"}
    }
);

export default StudentApps;