import React, {useEffect, useState, Fragment} from "react";
import axios, {CancelToken} from "axios";

function StudentContainer({source, notFound, loading, timestamp, children}) {
    const [apiCall] = useStudentApi({source, timestamp});
    return (
        <>
            {apiCall.loading
            ? loading
            : <StudentDisplay 
            students = {apiCall.students} 
            notFound = {notFound}
            formatAs = {children}
            />}
        </>
    )
}

function StudentDisplay({students, notFound, formatAs}) {
    if (!Array.isArray(students) || students.length < 1) return <p>{notFound}</p>;
    return students.map(student => (
        <Fragment key = {student.uuid}>{formatAs(student)}</Fragment>
    ));
}

function useStudentApi({source, timestamp}) {
    const [apiCall, setApiCall] = useState({loading: true, students: null});
    useEffect(() => {
        let cancelCall;
        function fetchStudents() {
            axios.get(source, {cancelToken: new CancelToken(cancelToken => cancelCall = cancelToken)})
            .then(response => { const students = response.data; setApiCall({loading: false, students}); })
            .catch(e => /*axios.isCancel(e)*/ undefined); //DO SOMETHING
        }
        fetchStudents();
        return () => cancelCall();
    }, [source, timestamp]);
    return [apiCall];
}

export default StudentContainer;