import React, {useContext} from "react"
import StudentContainer from "../../components/student/StudentInfo.js";
import {ToggleContext} from "../../components/toggleable/Toggleable.js";
import {StudentContext} from "../../context/StudentContext.js";
import {StudentUnsubButton, StudentEditButton} from "./StudentButtons.js";
import StudentPhoto from "./StudentPhoto.js";

function StudentInfo() {
    const student = useContext(StudentContext);
    const toggleable = useContext(ToggleContext);
    const formatDate = date => `${date.getUTCDate()} / ${date.getUTCMonth() + 1} / ${date.getUTCFullYear()}` 
    return (
        <StudentContainer source = {`/api/students?uuid=${student.uuid}`}
            notFound = "No se encontro alumno..."
            loading =  "Cargando..."
            timestamp = {toggleable.timestamp}
        >
            {student => ( //EDIT BUTTON HERE
                <div className = "student-data">
                    <StudentPhoto initialSource = {student["photo-src"]}/>
                    <div className = "student-info">
                        <p>Nombre: {student.name}</p> 
                        <p>Apellido: {student.lastname}</p>
                        <p>Fecha de Nacimiento: {formatDate(new Date(student.birth))}</p>
                        <p>Fecha de Ingreso: {formatDate(new Date(student.enroll))}</p>
                        <p>Nivel: {student.level}</p>
                        <p>Cuota: {student.fee}</p>
                        <p>Pago de Mes Actual: {student["pay-current"]}</p>
                        <p>Pago de Mes Anterior: {student["pay-last"]}</p>
                        <StudentEditButton caption = "Editar"/>
                        <StudentUnsubButton caption = "Dar de Baja"/>
                    </div>  
                </div>    
            )}   
        </StudentContainer>
    );
}

export default StudentInfo;
