import React, {useContext, useRef, useState} from "react";
import {StudentContext} from "../../context/StudentContext.js";

function StudentPhoto({initialSource}) {
    const studentUUID = useContext(StudentContext).uuid;
    const [source, setSource] = useState({src: initialSource, timestamp: Date.now()});
    const photo = useRef();
    function onPhotoSelect(event) { photo.current = event.target.files[0]; }
    function onPhotoUpload(event) { 
        event.preventDefault();
        fetch(`/api/students/upload/photo?uuid=${studentUUID}`, {method: "POST", body: photo.current})
        .then(response => response.json())
        .then(source => {
            setSource({src: source.src, timestamp: Date.now()});
        });    
    }
    function onPhotoError(event) { event.target.src = `./media/students/photo/placeholder.jpg`; }
    return (
        <div className = "student-photo">
            <img className = "student-avatar"
                src = {source.src 
                    ? `./media/students/${source.src}?${source.timestamp}` 
                    : `./media/students/photo/placeholder.jpg?${source.timestamp}`} 
                alt = "student-avatar"
                onError = {onPhotoError}
            />
            <form onSubmit = {onPhotoUpload}>
                <input type = "file" onInput = {onPhotoSelect}/>
                <button type = "submit"> Subir </button>
            </form> 
        </div>
    );
}

export default StudentPhoto;