import {useEffect, useState, useContext} from "react";
import {StudentContext} from "../context/StudentContext.js";
import axios, {CancelToken} from "axios";

function useRegularFields(studentFields) {
    const [regularFields] = useState(studentFields.fields);
    return [regularFields];
}

function useInitializedFields(studentFields, counts) {
    const [initializedFields, setInitializedFields] = useState(studentFields.fields);
    const studentUUID = useContext(StudentContext).uuid;
    function formatDate(rawdate) {
        const date = new Date(rawdate);
        const dateparts = [date.getUTCFullYear(), date.getUTCMonth(), date.getUTCDate()];
        return dateparts.map(part => String(part).padStart(2, "0")).join("-");
    }
    useEffect(() => {
        let cancelInit;
        function fetchFieldData() {
            axios.get(`/api/students?uuid=${studentUUID}`, {cancelToken: new CancelToken(cancelToken => cancelInit = cancelToken)})
            .then(response => { 
                if (!Array.isArray(response.data) || response.data.length !== 1) throw new Error();
                const student = response.data[0]; 
                setInitializedFields(rawFields => {
                    return Object.keys(rawFields).reduce((initialized, field) => {
                        initialized[field] = rawFields[field]; //COPY
                        initialized[field].defaultValue = 
                            rawFields[field].type === "date" ? formatDate(student[field]) : String(student[field]); //INITIALIZE VALUE
                        return initialized;
                    }, Object.create(null));
                });
            })
            .catch(e => /*axios.isCancel(e)*/ undefined); //DO SOMETHING
        }
        fetchFieldData();
        return () => cancelInit();
    }, [studentUUID, counts.submittedCount, counts.errorCount]);
    return [initializedFields];
}

export {useRegularFields, useInitializedFields};