import React, {useState} from "react";
import StudentContainer from "../student/StudentInfo.js";

function StudentSearch() {
    const [query, setQuery] = useState("$");
    return (
        <div className = "app-search">
            <SearchBar setQuery = {setQuery} />
            <SearchResults query = {query} />
        </div>
    )
}

function SearchBar({setQuery}) {
    return (
        <div className = "search-bar">
           <input type = "text" onChange = {event => {
               setQuery(event.target.value || "$");
           }}/>
        </div>
    );
}

function SearchResults({query}) {
    return (
        <div className = "search-results">
            <StudentContainer
                source = {`/api/students?autocomplete_fullname=${query}`}
                notFound = "Aqui se muestran los resultados encontrados..."
            >
                {student => (
                    <a className = "search-student-link" href = {`/student?uuid=${student.uuid}`}>
                        <div className = "search-student-info">
                            <p>{student.fullname}</p>
                            <p>Ingresó: {student.enroll.split("T")[0]}</p>
                            <p>Paga: ${student.fee}, Pagó este mes: ${student["pay-current"]}</p>
                        </div>
                    </a>
                )}
            </StudentContainer>
        </div>
    )
}

export default StudentSearch;